ter}.headerOne__title{font-size:50px;font-weight:bold;font-style:italic;color:darkred}.headerTwo__title{font-size:50px;font-weight:bold;color:darkslateblue}.headerThree__title{font-size:50px;font-weight:bold;color:darkorange}.mains{width:100%;height:100%;display:flex;justify-content:space-around;align-items:center;font-size:20px}.mainOne{background-color:darkseagreen}.mainTwo{background-color:darkslateblue}.mainThree{background-color:dodgerblue}.sprites{width:100%;height:100%;display:flex;justify-content:center;align-items:center;font-size:30px;font-weight:900}.footerOne__text{color:green}.footerTwo{color:goldenrod}.footerThree{color:saddlebrown}

